body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Roboto', sans-serif;
    cursor: default;
}

* a,
input {
    cursor: pointer;
}

body {
    background-color: darkgray;
}

/* HADER START */
header {
    height: 50px;
    width: 100%;
    background-color: #222831;
    box-shadow: 0px 25px 48px -16px rgba(0, 0, 0, 0.25);
}

/* SEARCH INPUT START */
.cpart {
    max-width: 1250px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
}

.searchinput {
    position: absolute;
    height: 100%;
    right: 0;
    display: flex;
    align-items: center;
    padding: 20px;
}

.searchinput input {
    font-family: 'Zen Kurenaido', sans-serif;
    padding: 5px;
    border: none;
    background-color: #222831;
    color: white;
    border-bottom: 1px white solid;
    font-size: 16px;
    max-width: 100px;
    min-width: 100px;
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -o-transition: all .5s;
    -ms-transition: all .5s;
}

.searchinput input:focus {
    width: 300px;
    max-width: 300px;
    min-width: 300px;
}

.searchinput input:not(:-ms-input-placeholder) {
    width: 300px;
    max-width: 300px;
    min-width: 300px;
}

.searchinput input:not(:placeholder-shown) {
    width: 300px;
    max-width: 300px;
    min-width: 300px;
}

.searchinput input:focus {
    outline: none;
}

.searchinput input::-webkit-input-placeholder {
    color: white;
    opacity: 1;
}

.searchinput input:-ms-input-placeholder {
    color: white;
    opacity: 1;
}

.searchinput input::placeholder {
    color: white;
    opacity: 1;
}

.searchinput input:-ms-input-placeholder {
    color: white;
}

.searchinput input::-ms-input-placeholder {
    color: white;
}

/* SEARCH INPUT END */

/* LOGO START */
.logo {
    position: absolute;
    height: 100%;
    left: 0;
    display: flex;
    align-items: center;
    padding: 20px;
}

.logo a {
    text-decoration: none;
    color: white;
    font-family: 'Zen Kurenaido', sans-serif;
    font-size: 24px;
    font-weight: bold;
}

/* LOGO END */

/* HEADER END */

/* MAIN START */

.indexmain {
    width: 100%;
}

.elements {
    max-width: 1250px;
    width: 100%;
    height: 100%;
    margin: 0 auto;
    position: relative;
    padding: 20px;
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    /* ZMNIEJSZYC GAP PO BREAKPOINT */
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-template-rows: -webkit-max-content;
    grid-template-rows: max-content;
    grid-auto-rows: -webkit-max-content;
    grid-auto-rows: max-content;
}

/*POST START*/

.post {
    grid-column: span 1;
    position: relative;
    background-color: #222831;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    border-radius: 2px;
    overflow: hidden;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
    -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
    transition: all .5s;
    -webkit-transition: all .5s;
    -moz-transition: all .5s;
    -o-transition: all .5s;
    -ms-transition: all .5s;
    top: 0px;
}

.post:first-child {
    grid-column: 1/-1;
}

.post:first-child img {
    height: 500px;
}

.post:first-child .postdescription .tbtm {
    margin: 0;
}

.post img {
    height: 250px;
    object-fit: cover;
}

.postdescription {
    height: 100%;
    padding: 15px;
    display: flex;
    flex-direction: column;
    background-color: #222831;
}

.postdescription a h2,
.postdescription a h5 {
    cursor: pointer;
}

.postdescription h2 {
    margin-bottom: 10px;
    font-weight: 400;
    color: #EEEEEE;
}

.postdescription h5 {
    margin-top: 5px;
    font-weight: 300;
    color: dimgray;
}

.postdescription .tbtm {
    margin-top: auto;
    color: lightslategray;
    text-align: justify;
}

.post:hover {
    top: -5px;
}

.post:first-child:hover {
    top: 0px;
}

/*POST END*/
.loadwrapper {
    max-width: 1250px;
    padding: 20px;
    margin: 0 auto;
}

.load {
    height: 75px;
    width: 100%;
    background-color: #222831;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
    -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
    -moz-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.25);
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Zen Kurenaido', sans-serif;
    color: aliceblue;
    font-weight: 100;
    cursor: pointer;
    transition: all .5s;
    margin-bottom: 10px;
}

.load:hover {
    background-color: #393E46;
}

/* MAIN END */

/* FOOTER */
footer {
    height: 100px;
    background-color: #222831;
    display: flex;
    flex-direction: column;
    place-content: center;
}

footer h2 {
    font-family: 'Zen Kurenaido', sans-serif;
    text-align: center;
    font-size: 14px;
    color: dimgray;
    font-weight: 100;
}

/* FOOTER END */

/* POST */
main {
    padding: 10px;
}

.postcontent {
